.container {
    margin-right: 24px;
    margin-bottom: 100px;
    max-width: 800px;
}

.container h1 {
    color: var(--colors-grey900);
    font-size: 24px;
    font-weight: 500;
}

.container h2 {
    font-size: 16px;
    margin: 20px 0 8px;
}

.container p {
    line-height: 18px;
    font-size: 15px;
    margin: 6px 0 12px;
}

.container iframe {
    display: block;
    width: 100%;
    height: auto;
    aspect-ratio: 16 / 9;
    margin: 20px auto;
    border: 0;
}
